.conteo{
    opacity: 0;
    animation: fadeIn 1s forwards 0.9s;
    position: relative;
    text-align: center;
    width: 100%;
    height: 100vh;
}

@keyframes fadeIn{
    0% {opacity: 0}
    100% {opacity: 1}
}

.logofep_conteo{
    background: url('../../assets/img/generales/logofep.png');
    background-size: cover;
    background-repeat: no-repeat;
    width: 300px;
    height: 90px;
    margin: 0px auto;
    margin-top: 15px;
    margin-bottom: 60px;
}

#demo{
    font-size: 100px;
    font-family: 'TitlingGothicFBWide-Bold';
    padding: 0 30px;
}

#demo > div{
    display: flex;
    justify-content: center;

}

#demo div div{
    display: flex;
    flex-direction: column;
    margin: 0 35px;
}

#demo p{
    font-size: 12px;
    margin-top: -10px;
    color: #fda302;
    text-transform: uppercase;
}

#demo span{
    display: inline-block;
    color: #fda302;
    margin: 0 15px;
}

.tiempos {
    display: flex;
    justify-content: center;
}

.contenedor{
    display: flex;
    justify-content: center;
    transition: 0.5s;
}

.contenedor a{
    color: #2c267b !important;
    text-decoration: none;
}

.contenedor div{
    margin: 50px 25px;
    padding: 15px 25px;
    background: #fda302;
    color: #2c267b;
    cursor: pointer;
    width: 25%;
    font-size: 13px;
    -webkit-box-shadow: 10px 10px 23px -9px rgba(0,0,0,0.75);
    -moz-box-shadow: 10px 10px 23px -9px rgba(0,0,0,0.75);
    box-shadow: 10px 10px 23px -9px rgba(0,0,0,0.75);
	font-family: 'TitlingGothicFBWide-Bold';
}

.contenedor_form{
    position: absolute;
    width: 100%;
    transform: translate(0px, -50%);
    opacity: 0;
    visibility: hidden;
    transition: 1s;
}

.content_colombia{
    font-family: 'TitlingGothicFBWide-Bold';
    font-size: 13px;
    padding: 25px;
}


.fadeOut{
    opacity: 0;
    visibility: hidden;
}

.fadeIn_form{
    opacity: 1 !important;
    visibility: visible !important;
}

@media only screen and (min-width: 2000px) and (min-height: 1000px){
    #demo {
        font-size: 150px;

    }

    .content_colombia {
        font-size: 25px;
    }

}

@media only screen and (min-width: 1000px) and (min-height: 500px) and (max-height: 550px){
    #demo {
        font-size: 50px;
    }

    .logofep_conteo{
        width: 225px;
        height: 70px;
        margin-top: 15px;
        margin-bottom: 15px;
    }

    .contenedor {
        margin-top: 45px;
    }

    .contenedor_form {
        transform: translate(0px, -88%);
    }
}

@media only screen and (min-width: 1000px) and (min-height: 550px) and (max-height: 650px) {
    .contenedor_form {
        transform: translate(0px, -76%);
    }

    .logofep_conteo{
        width: 225px;
        height: 70px;
        margin-top: 15px;
        margin-bottom: 15px;
    }

    #demo {
        font-size: 60px;
    }

    .contenedor {
        margin-top: 45px;
    }
}

@media only screen and (min-width: 1000px) and (min-height: 650px) and (max-height: 750px) {
    .contenedor_form {
        transform: translate(0px, -65%);
    }

    .logofep_conteo{
        width: 225px;
        height: 70px;
        margin-top: 15px;
        margin-bottom: 15px;
    }

    #demo {
        font-size: 60px;
    }
    
    .contenedor {
        margin-top: 45px;
    }
}

@media only screen and (max-width: 768px){

    #demo {
        font-size: 40px;
    }

    #demo div div{
        margin: 0 15px;
    }


    #demo p{
        font-size: 8px;
        margin-top: 0px;
    }

    .contenedor{
        display: block;
    }

    .contenedor div{
        margin: 0px auto;
        width: 50%;
        margin-bottom: 30px;
        margin-top: 25px;
    }

    .contenedor_form {
        transform: translate(0px, -43%);
    }


}

@media only screen and (max-width: 430px){

    .contenedor div {
        width: 60%;
    }

    .contenedor_form {
        transform: translate(0px, -50%);
    }


    .logofep_conteo {
        margin-top: 65px;
        margin-bottom: 30px;
    }

    
    #demo {
        font-size: 25px;
    }

    #demo div div{
        margin: 0 0px;
    }

    
    #demo p{
        font-size: 5px;
        margin-top: 0px;
    }

    .contenedor div {
        font-size: 10px;
    }

    .content_colombia {
        width: 90%;
        margin: 0px auto;
    }


    
}

@media only screen and (max-width: 320px){
    #demo {
        font-size: 20px;
    }

    
    .contenedor_form {
        transform: translate(0px, -58%);
    }
    
}